body {
  font-family: sans-serif;
  margin: -2px;
  background: #63856a; /* Old browsers */
  background: -moz-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* FF3.6+ */
  background: -webkit-gradient(
    radial,
    center center,
    0px,
    center center,
    100%,
    color-stop(1%, #63856a),
    color-stop(100%, #3c5a40)
  ); /* Chrome,Safari4+ */
  background: -webkit-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* Chrome10+,Safari5.1+ */
  background: -o-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* Opera 12+ */
  background: -ms-radial-gradient(
    center,
    ellipse cover,
    #63856a 1%,
    #3c5a40 100%
  ); /* IE10+ */
  background: radial-gradient(
    ellipse at center,
    #63856a 1%,
    #3c5a40 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#63856a', endColorstr='#3c5a40',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.header {
  padding: 8px;
  border-bottom: 1px solid rgb(205, 206, 187);
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  color: rgb(205, 206, 187);
  letter-spacing: 0.75px;
}

.footer {
  margin-top: 16px;
  padding: 6px;
  border-top: 1px solid rgb(205, 206, 187);
  text-align: center;
  font-size: 14px;
  font-weight: 600;
  color: rgb(205, 206, 187);
  letter-spacing: 0.75px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.content {
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 48px;
}

.content h4 {
  max-width: 480px;
}

html,
body {
  min-height: 100%;
}

.stem-students {
  height: auto;
  width: 360px;
  border: 2px solid rgb(143, 85, 85);
}

.image-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

h4 {
  color: DarkKhaki;
  letter-spacing: 1px;
  line-height: 28px;
}
